.donation__block {
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 80px;
    margin: 30px 0;
}

.donation__block-item {
    min-width: 300px;
    padding: 20px;
    border: 1px solid;
}

.donation__block-item a {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: var(--color-white);
    text-align: center;
}

.donation__block-item h2 {
    display: inline;
    padding-bottom: 3px;
    border-bottom: 1px solid transparent;
}

.donation__block-item:hover h2 {
    border-bottom: 1px solid;
}

.donation__block-item img {
    display: block;
    margin: 0 auto 20px;
    max-width: 300px;
}

@media screen and (max-width: 767px) {
    .donation__block{
        gap: 20px;
        flex-direction: column;
    }

    .donation__block-item {
        min-width: 0;
        width: 100%;
        box-sizing: border-box;
        height: 300px;
    }

    .donation__block-item img {
        max-width: 100%;
    }
}