.hello-banner {
    background-color: var(--color-dark-green);
    color: var(--color-white);
    max-height: 400px;
    transition: all 1s ease-out;
}

.hello-banner--hidden {
    max-height: 0;
    overflow: hidden;
    transition: all 1s ease-out;
}

.hello-banner .wrapper {
    display: flex;
    justify-content: center;
    gap: 40px;
    padding: 15px 0;
}

@media screen and (max-width: 767px) {
    .hello-banner .wrapper { 
        flex-direction: column;
        gap: 20px;
    }

    .hello-banner h2 {
        font-size: 20px;
    }
}