.header {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: var(--color-white);
    box-shadow: 0 0 11px rgb(33 33 33 / 20%);
}

.header__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0;
    position: relative;
}

.header__navigation-block {
    display: contents;
}

.header__logo-text {
    font-size: 16px;
    font-family: var(--font-extra-bold);
    max-width: 220px;
    text-transform: uppercase;
    text-align: left;
}

.hamburger-react {
    display: none;
}

@media screen and (max-width: 991px) {
	.header__logo-text {
		max-width: 130px;
	}
}

@media screen and (max-width: 767px) {
    .hamburger-react {
        display: block;
    }

    .header__navigation-block {
        display: none;
        position: absolute;
        width: 100vw;
        top: 100%;
        left: -30px;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 40px;
        background-color: var(--color-white);
        box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
        padding: 30px 0;
    }

    .header__block--menu-active .header__navigation-block {
        display: flex;
    }
}