.titled-image {
    width: 150px;
}

.titled-image__item,
.titled-image__logo {
    width: 100%;
}

.titled-image__title {
    font-family: 'MacPawFixelDisplay-ExtraBold', Georgia, sans-serif;
    margin-top: 10px;
    text-align: center;
}

@media screen and (max-width: 991px) {
    .titled-image__item {
        width: 90%;
        margin: 0 auto;
    }
}