.footer__block {
    display: flex;
    justify-content: space-between;
}

.footer__block-item {
    width: 30%;
}

.footer__disclaimer-title,
.footer__info-title {
    font-family: 'MacPawFixelDisplay-ExtraBold', Georgia, sans-serif;
    font-size: 18px;
}

.footer__disclaimer-description {
    font-family: 'MacPawFixelDisplay-Regular', Georgia, sans-serif;
    font-size: 12px;
    margin-top: 15px;
}

.footer__info-title {
    text-transform: uppercase;
}

.footer__info-list {
    margin-top: 15px;
}

.footer__info-list-item button {
    margin-top: 5px;
    color: var(--color-white);
    font-size: 16px;
    text-transform: capitalize;
    padding-bottom: 1px;
    border-bottom: 1px solid transparent;
}

.footer__info-list-item button:hover {
    border-bottom: 1px solid;
}

.footer__social-block {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;
}

.footer__contacts-description {
    margin-top: 15px;
}

.footer__contacts-paragraph {
    color: var(--color-white);
    margin-top: 5px;
}

.footer__contacts-link {
    color: var(--color-white);
    padding-bottom: 4px;
    border-bottom: 1px dashed;
}

.footer__info-title {
    margin-bottom: 15px;
}

.footer__copyright {
    font-size: 12px;
}

@media screen and (max-width: 767px) {
    .footer__block {
        flex-direction: column;
        gap: 20px;
    }

    .footer__block-item {
        width: 100%;
    }
}