.slick-prev, 
.slick-next {
    width: 30px;
    height: 30px;
}

.slick-prev:before, 
.slick-next:before {
    font-size: 30px;
    color: var(--color-primary-dark);
    opacity: 1;
}

.slick-prev {
    left: -30px;
}

.slick-next {
    right: -30px;
}

.slick-dots {
    bottom: -35px;
}

.slick-dots li button:before {
    font-size: 15px;
}