.member__card {
    display: flex;
    flex-direction: column;
    width: 370px;
}

.member__card-avatar {
    width: 100%;
    height: auto;
}

.member__card-info {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
}

.member__card-name {
    font-family: var(--font-extra-bold);
    font-size: 22px;
}

.member__card-inst {
    width: 30px;
    height: 30px;
}

.member__card-title {
    font-size: 22px;
}

.member__card-description {
    font-size: 15px;
}

@media screen and (max-width: 767px) {
    .member__card-title {
        font-size: 15px;
    }

    .member__card-description {
        margin-top: 10px;
    }
}