.card {
    background-color: var(--color-white);
    color: var(--color-dark-grey);
    padding: 35px 18px;
    width: 30%;
}

.card__image {
    width: 160px;
    height: 160px;
    display: block;
    margin: 0 auto;
    border-radius: 50%;
    background-color: var(--color-light-grey);
}

.card__image img {
    height: auto;
    width: 100%;
    border-radius: 50%;
}

.card__title {
    font-family: var(--font-extra-bold);
    text-align: center;
    margin-top: 22px;
    text-transform: uppercase;
}

.card__description {
    margin-top: 7px;
}

@media screen and (max-width: 767px) {
    .card {
        width: 100%;
        box-sizing: border-box;
    }
}