.trusted_list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 40px;
    gap: 50px;
}

@media screen and (max-width: 991px) {
    .trusted_list.slick-slider {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: 767px) {
    .trusted_list {
        margin-bottom: 30px;
    }
}