.scroll-top__button {
    position: fixed;
    bottom: 45px;
    right: 30px;
    width: 40px;
    height: 40px;
}

.scroll-top__button button {
    width: 100%;
    height: 100%;
    background-color: var(--color-primary-dark);
    border: 1px solid var(--color-primary-light);
    position: relative;
}

.scroll-top__button button::before {
    position: absolute;
    content: '';
    top: 40%;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    width: 15px;
    height: 15px;
    border-left: 3px solid var(--color-primary-light);
    border-top: 3px solid var(--color-primary-light);
}

.scroll-top__button button:hover {
    background-color: var(--color-primary-light);
    border: 1px solid var(--color-primary-dark);
}

.scroll-top__button button:hover::before {
    border-left: 3px solid #2b4ba8;
    border-top: 3px solid #2b4ba8;
}