.language__switcher {
    display: flex;
    gap: 16px;
    position: relative;
}

.language__switcher::after {
    content: '';
    position: absolute;
    top: 0;
    right: 50%;
    transform: translate(calc(-50% + 3px));
    width: 3px;
    height: 20px;
    background-color: var(--color-primary-light);
}

.language__switcher-link {
    font-size: 15px;
    text-transform: uppercase;
    color: var(--color-dark-grey);
    font-family: var(--font-regular);
    padding-bottom: 1px;
    border-bottom: 1px solid transparent;
}

.language__switcher-link:not(.language__switcher-link--active):hover {
    border-bottom: 1px solid;
}

.language__switcher-link--active {
    color: var(--color-primary-dark);
    font-family: var(--font-extra-bold);
}