.title {
    padding: 5px 10px;
    font-size: 26px;
    font-family: 'MacPawFixelDisplay-ExtraBold', Georgia, sans-serif;
    text-transform: uppercase;
    line-height: 1;
    text-align: center;
    position: relative;
    display: block;
    width: fit-content;
    margin: 0 auto;
}

.title--light {
    color: var(--color-primary-light);
}

.title--dark {
    color: var(--color-primary-dark);;
}

.title::before,
.title::after {
    position: absolute;
    content: '';
    top: -1px;
    width: 7px;
    height: 100%;
    border-top: 2px solid;
    border-bottom: 2px solid;
}

.title::before {
    border-left: 2px solid;
    left: 0;
}

.title::after {
    border-right: 2px solid;
    right: 0;
}

.title.title--light::before, 
.title.title--light::after {
    border-color: var(--color-primary-light);
}

.title.title--dark::before, 
.title.title--dark::after {
    border-color: var(--color-primary-dark);
}