.team__block {
    display: flex;
    justify-content: center;
    gap: 40px;
    margin-top: 30px;
}

.team__description {
    max-width: 50%;
    margin: 30px auto 0 auto;
    font-size: 22px;
    text-align: center;
}

@media screen and (max-width: 991px) {
    .team__description {
        max-width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .team__block {
        gap: 20px;
    }
}