.hero__block {
    display: flex;
    align-items: flex-start;
    gap: 80px;
}

.hero__text {
    margin-top: 25px;
}

.hero__text p {
    margin-top: 7px;
    line-height: 1.5;
}

@media screen and (max-width: 991px) {
    .hero__block {
        gap: 0;
    }
}

@media screen and (max-width: 767px) {
    .hero__block .logo {
        display: none;
    }
}