.fundraising__card {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 40px;
    margin-top: 50px;
}

.fundraising__image-block {
    width: 40%;
}

.fundraising__image-block img {
    width: 100%;
}

.fundraising__text-block {
    width: 50%;
}

.fundraising__text-block h2 {
    margin-bottom: 15px;
}

.fundraising__text-block p {
    font-size: 20px;
}

.fundraising__social-block {
    margin-top: 30px;
}

.fundraising__social {
    margin-top: 15px;
    max-width: 170px;
}

@media screen and (max-width: 767px) {
    .fundraising__card {
        flex-direction: column;
    }

    .fundraising__image-block,
    .fundraising__text-block {
        width: 100%;
    }

    .fundraising__social {
        margin: 15px auto 0 auto;
        width: 100%;
        max-width: max-content;
    }
}