.nav__block {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
}

.nav__link {
    font-size: 18px;
    font-family: 'MacPawFixelDisplay-ExtraBold', Georgia, sans-serif;
    text-transform: uppercase;
    padding-bottom: 1px;
    border-bottom: 1px solid transparent;
}

.nav__link:hover {
    border-bottom: 1px solid;
}

@media screen and (max-width: 991px) {
    .nav__block {
        gap: 20px;
    }

    .nav__link {
        font-size: 16px;
    }
}

@media screen and (max-width: 767px) {
    .nav__block {
        flex-direction: column;
    }
}