.priorities__block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 30px;
    gap: 40px;
}

.priorities__block .card:nth-of-type(1) .card__image img {
    width: 109%;
    margin-left: -13px;
    margin-top: -7px;
}

.priorities__block .card:nth-of-type(2) .card__image img {
    width: 136%;
    margin-left: -35px;
    margin-top: -22px;
}

.priorities__block .card:nth-of-type(3) .card__image img {
    width: 145%;
    margin-left: -36px;
    margin-top: -12px;
}

@media screen and (max-width: 991px) {
    .priorities__block {
        gap: 15px;
    }
}

@media screen and (max-width: 767px) {
    .priorities__block {
        flex-direction: column;
    }
}